.reading-progress-bar {
    position: fixed;
    height: 5px;
    top: 0;
    background-color: #10D397;
    
    
  }
  
#img {
    height: 80%;
    width: 80%;
}
.btn {

        color: #10D397 !important;
        border-color: #10D397 !important; 

    }



#home-title {
    color: #97999B;
    font-size: 52px;
}
.home-1 {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.home-2 {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (min-width: 900px) {
    .home {
        margin-top: 110px;
        height: 100vh;
    }
}

#home-title span {
    color: #10D397;
}
.home-cont {
    display: flex;
    flex-direction: row;
}
.home {
    height: 100vh;
}
#home-img {
    height: 100%;
    width: 100%;
}
#cont {
    height: 100%;
}
@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .home-cont {
        display: flex;
        flex-direction: column;
    }
    .home-1 {
        width: 100%;
        height: 50%;
    }
    .home-2 {
        width: 100%;
        height: 50%;
    }
    #home-img {
        height: 80%;
        width: 80%;
    }
   
    #home-title {
        font-size: 46px;
    }
  }
  @media screen and (min-device-width: 768px) and (max-device-width: 1024px) { 
    .home-cont {
        display: flex;
        flex-direction: column;
    } 
    .home-1 {
        width: 100%;
        height: 50%;
    }
    .home-2 {
        width: 100%;
        height: 50%;
    }
    #home-title {
        font-size: 60px;
        text-align: center;
        padding: 40px;
        
    } 
    #home-img {
        height: 90%;
        width: 90%;
        padding-top: 60px;
    }
}
.about-1 {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.about-2 {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

 
.about {
    
    height: 100vh;
}
.about-cont {
    display: flex;
    flex-direction: row;
}

#about-text {
    padding-top: 50px;
    font-size: 26px;
    text-align: center;
    color: #97999B;
}
#about-title {
    color: #10D397;
}
#about-text span {
        color: #10D397;

}

@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .about-cont {
        display: flex;
        flex-direction: column;
    }
    .about-1 {
        width: 100%;
        height: 50%;
    }
    .about-2 {
        width: 100%;
        height: 50%;
        padding-top: 40px;
    }
    #img-about {
        width: 100%;
    }
    #about-text {
        font-size: 20px;
        
    }
  }
  @media screen and (min-device-width: 768px) and (max-device-width: 1024px) { 
    .about-cont {
        display: flex;
        flex-direction: column;
    }
    .about-1 {
        width: 100%;
        height: 50%;
    }
    .about-2 {
        width: 100%;
        height: 50%;
        padding-top: 40px;
    }
    #about-text {
        font-size: 38px;
        
    }
    #about-title {
        font-size: 50px;
    }
  }
.about2 {
    height: 100vh;
    width: 100vw;
    justify-content: center;
    align-items: center;
}

#about2-title {
    color: #10D397;
    font-size: 45px;
    text-align: center;
}
.card-container {
    width: 100%;
    height: 70%;
    padding-top: 40px;
}



@media only screen and (max-width: 768px) {
    #about2-title {
        color: #10D397;
        font-size: 30px;
        text-align: center;
    }
    .about2 {
        height: 170vh;

    }
}
@media screen and (min-device-width: 768px) and (max-device-width: 1024px) { 
    #card-cont {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .col {
        width: 70%;
    }
    #about2-title {
        color: #10D397;
        font-size: 55px;
        text-align: center;
}
}
